import { useEffect } from 'react';
import { useMonacoBase } from '@/hooks/useMonacoBase';
export const useMonacoEditor = (options) => {
    const { value, onChange, language = 'plaintext', theme, readOnly, automaticLayout, } = options;
    const { editorRef, containerRef, isInitializedRef, initMonacoEditor, } = useMonacoBase();
    useEffect(() => {
        const createEditor = (monaco, container, config) => {
            const editor = monaco.editor.create(container, {
                value,
                language,
                ...config,
            });
            editor.onDidChangeModelContent(() => {
                const newValue = editor?.getValue() || '';
                onChange(newValue);
            });
            return editor;
        };
        initMonacoEditor(createEditor, { theme, readOnly, automaticLayout });
    }, [language, onChange]); // 只依赖创建时的配置
    // 更新编辑器内容
    useEffect(() => {
        if (editorRef.current && isInitializedRef.current) {
            const codeEditor = editorRef.current;
            const currentValue = codeEditor.getValue();
            if (currentValue !== value) {
                codeEditor.setValue(value);
            }
        }
    }, [value]);
    const focusEditor = () => {
        const codeEditor = editorRef.current;
        codeEditor?.focus();
    };
    return {
        containerRef,
        focusEditor,
        editor: editorRef.current,
    };
};
