import { useEffect } from 'react';
import { useMonacoBase } from '@/hooks/useMonacoBase';
export const useMonacoDiffEditor = (options) => {
    const { leftText, rightText, viewMode, theme, } = options;
    const { editorRef, containerRef, isInitializedRef, initMonacoEditor, loadMonaco, } = useMonacoBase();
    useEffect(() => {
        const createDiffEditor = (monaco, container, config) => {
            const editor = monaco.editor.createDiffEditor(container, {
                ...config,
                renderSideBySide: viewMode === 'split',
                enableSplitViewResizing: true,
                renderLineHighlight: 'all',
                readOnly: true,
            });
            // 立即设置初始内容
            const originalModel = monaco.editor.createModel(leftText, 'plaintext');
            const modifiedModel = monaco.editor.createModel(rightText, 'plaintext');
            editor.setModel({
                original: originalModel,
                modified: modifiedModel,
            });
            return editor;
        };
        initMonacoEditor(createDiffEditor, { theme });
    }, []); // 只在组件挂载时创建一次
    // 更新diff内容
    useEffect(() => {
        const updateDiff = async () => {
            if (editorRef.current && isInitializedRef.current) {
                try {
                    const monaco = await loadMonaco();
                    const originalModel = monaco.editor.createModel(leftText, 'plaintext');
                    const modifiedModel = monaco.editor.createModel(rightText, 'plaintext');
                    const diffEditor = editorRef.current;
                    diffEditor.setModel({
                        original: originalModel,
                        modified: modifiedModel,
                    });
                }
                catch (error) {
                    console.error('Failed to update diff model:', error);
                }
            }
        };
        if (isInitializedRef.current) {
            updateDiff();
        }
    }, [leftText, rightText]);
    return {
        containerRef,
        editor: editorRef.current,
    };
};
